import React from "react";

const educationContent = [
  {
    year: "Oct 2009 - Sep 2012",
    degree: "computer science",
    institute: 'Technical School "15. maj"',
    details: `The school offers education programs in several technical fields, including
    mechanical engineering, electrical engineering, automotive engineering, and
    computer science.`,
  },
  {
    year: "Oct 2012 - Sep 2016",
    degree: "Bachelor of Engineering",
    institute: 'Kragujevac University',
    details: `University of Kragujevac is a modern education and research center embracing
    almost all major areas of teaching and research, with a student population of
    18.000 and 1.200 academic staff`,
  },
];

const Education = () => {
  return (
    <ul>
      {educationContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.degree}
            <span className="place open-sans-font">{val.institute}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};

export default Education;
