import React from "react";

const skillsContent = [
  { skillClass: "p100", skillPercent: "100", skillName: "React JS" },
  { skillClass: "p90", skillPercent: "90", skillName: "Next JS" },
  { skillClass: "p100", skillPercent: "100", skillName: "Vue JS" },
  { skillClass: "p90", skillPercent: "90", skillName: "Nuxt JS" },
  { skillClass: "p90", skillPercent: "90", skillName: "Redux / Vuex" },
  { skillClass: "p90", skillPercent: "90", skillName: "Angular" },
  { skillClass: "p100", skillPercent: "100", skillName: "JS / TS" },
  { skillClass: "p90", skillPercent: "90", skillName: "Graph QL" },
  { skillClass: "p100", skillPercent: "100", skillName: "HTML / CSS" },
  { skillClass: "p100", skillPercent: "100", skillName: "Bootstrap 5" },
  { skillClass: "p100", skillPercent: "100", skillName: "Tailwind CSS" },
  { skillClass: "p90", skillPercent: "90", skillName: "Semantic / Material UI" },
];

const Skills = () => {
  return (
    <>
      {skillsContent.map((val, i) => (
        <div className="col-6 col-md-3 mb-3 mb-sm-5" key={i}>
          <div className={`c100 ${val.skillClass}`}>
            <span>{val.skillPercent}%</span>
            <div className="slice">
              <div className="bar"></div>
              <div className="fill"></div>
            </div>
          </div>
          <h6 className="open-sans-font text-center mt-2 mt-sm-4">
            {val.skillName}
          </h6>
        </div>
      ))}
    </>
  );
};

export default Skills;
