import React, { useState } from "react";
import Modal from "react-modal";
import Index from "./about/index";

const heroContent = {
  heroImage: "img/hero/dark.jpg",
  heroMobileImage: "img-mobile",
  heroTitleName: "Jusmin Lukan",
  heroDesignation: "Senior Front-End Developer",
  heroDescriptions: `I am a senior front-end developer with extensive experience specializing in developing and implementing the user interface (UI) and user experience (UX) of a website or application. I have advanced skills in React/Next, Vue/Nuxt, JS/TS and other web development technologies and strong leadership and communication skills. I can translate design mockups into functional, responsive web pages and manage multiple projects simultaneously. My role is providing an effective and engaging user experience that meets end-user needs and supports business goals.`,
  heroBtn: "about me",
};

const Hero = () => {
  const [isOpen, setIsOpen] = useState(false);
  function toggleModalOne() {
    setIsOpen(!isOpen);
  }

  return (
    <>
      <div className="row home-details-container align-items-center">
        
        <div className="col-lg-8 home-details text-center">
          <div>
            <img src='/img/hero/img-mobile.jpg' width="250px" height="250px" style={{borderRadius:10, marginTop:-50}}></img>
            <h1 className="poppins-font text-lg-start">
              I am {heroContent.heroTitleName}.
              <span className="description">{heroContent.heroDesignation}</span>
            </h1>
            <p className="open-sans-font text-lg-start">{heroContent.heroDescriptions}</p>
            <button className="button" onClick={toggleModalOne}>
              <span className="button-icon fa fa-arrow-right"></span>
              <span className="button-text">{heroContent.heroBtn}</span>
            </button>
          </div>
        </div>
        <div
          className="bg col-lg-4 d-none d-lg-block"
          style={{
            backgroundImage: `url(${
              process.env.PUBLIC_URL + heroContent.heroImage
            })`,
          }}
        ></div>
      </div>

      <Modal
        isOpen={isOpen}
        onRequestClose={toggleModalOne}
        contentLabel="My dialog"
        className="custom-modal hero"
        overlayClassName="custom-overlay"
        closeTimeoutMS={500}
      >
        <div>
          <button className="close-modal" onClick={toggleModalOne}>
            <img src="/img/cancel.svg" alt="close icon" />
          </button>
          {/* End close icon */}

          <div className="box_inner about">
            <div data-aos="fade-up" data-aos-duration="1200">
              <div className="title-section text-left text-sm-center">
                <h1>
                  ABOUT <span>ME</span>
                </h1>
                <span className="title-bg">Resume</span>
              </div>
              {/* End title */}
              <Index />
            </div>
          </div>
        </div>
        {/* End modal box news */}
      </Modal>
      {/* End  Modal for About More */}
    </>
  );
};

export default Hero;
